body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidebar-body {
  top: 0;
  left: 0;
  background-color: rgba(39, 44, 50, 1);
  width: 180px;
  min-width: 180px;
  box-shadow: 4px 6px 8px 0px rgba(0,0,0,0.2);
  height: 100vh;
  position: sticky;
  position: -webkit-sticky;
}

.sidebar-title {
  color: white;
  display: inline-block !important;
  height: 62px;
  width: 100%;
  font-size: 20px !important;
  padding-top: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.sidebar-title i:first-child {
  margin-right: 14px;
  margin-left: 10px;
  margin-top: -7px;
  font-size: 23px;
  float: left;
  background-color: grey;
  color: white;
  height: 42px;
  width: 42px;
  padding: 7px 7px 8px 8.5px;
  border-radius: 50%;
}

.sidebar-body i {
  text-align: center;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  font-size: 17px;
  float: left;
  margin-top: 0px;
  padding-top: 4px;
  margin-left: 10px;
  padding-right: 8px;
}

.sidebar-btn {
  padding: 6px 4px 6px 6px;
  width: 100%;
  font-size: 18px;
  color: white;
  display: inline-block;
  height: 40px;
  text-decoration: none;
}

.sidebar-btn:hover {
  text-decoration: none;
  color: lightGrey;
}

.sidebar-btn-active {
  background-color: rgba(255,255,255,0.1);
  text-decoration: none;
  color: white;
}

@media screen and (max-width: 600px) {
  .sidebar {
    display: none;
  }
}

.topnav-body {
  position: fixed;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
}

.topnav {
  background-color: white;
  height: 62px;
  width: 100%;
  box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.2);
}

.topnav a {
  float: left;
  display: block;
  color: grey;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.topnav-title {
  display: inline-block !important;
  height: 62px;
  font-size: 20px !important;
  margin-top: 3px;
}

.topnav-title i {
  margin-right: 14px;
  margin-left: 10px;
  margin-top: -7px;
  font-size: 23px;
  float: left;
  background-color: grey;
  color: white;
  height: 42px;
  width: 42px;
  padding: 7px 7px 8px 8.5px;
  border-radius: 50%;
}

.dropdown {
  float: right;
  display: inline-block;
  right: 10px;
}

.dropbtn {
  font-size: 17px;
  border: none;
  outline: none;
  color: grey;
  padding: 14px 16px;
  background-color: white;
  height: 62px;
}

.dropbtn i {
  margin-left: 6px;
}

.dropbtn-icon {
  display: none;
  border: none;
  outline: none;
  background-color: transparent;
}

.rotate {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.dropbtn:hover {
  color: black;
}

.dropdown-content-hidden {
  visibility: hidden;
  z-index: -1;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  transition: z-index 250ms step-start, visibility 250ms step-end, -webkit-transform 250ms ease-out;
  transition: z-index 250ms step-start, transform 250ms ease-out, visibility 250ms step-end;
  transition: z-index 250ms step-start, transform 250ms ease-out, visibility 250ms step-end, -webkit-transform 250ms ease-out;
}

.dropdown-content-visible {
  visibility: visible;
  z-index: 1;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  transition: z-index 250ms step-end, -webkit-transform 250ms ease-out;
  transition: z-index 250ms step-end, transform 250ms ease-out;
  transition: z-index 250ms step-end, transform 250ms ease-out, -webkit-transform 250ms ease-out;
}

.dropdown-content {
  right: 0;
  position: absolute;
  background-color: white;
  min-width: 170px;
  --box-shadow: 2px 2px 7px 2px rgba(0,0,0,0.2);
  box-shadow: 0px 2px 7px 0px rgba(0,0,0,0.2);
  top: 62px;
  border-radius: 0px 0px 0px 5px;
}

.dropdown-content a {
  float: none;
  color: grey;
  padding: 14px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:active {
  background-color: rgba(0, 0, 0, 0.4);
}

.dropdown-content a:not(:last-child){
  border-bottom: solid 1px;
  border-color: rgba(0, 0, 0, 0.10);
}

.topnav a:hover, .dropdown:hover .dropbtn {
  --background-color: rgba(0, 0, 0, 0.05);
  color: black;
}

.topnav .dropbtn-icon i {
  font-size: 20px;
  color: grey;
}

@media screen and (max-width: 600px) {
  .dropdown .dropbtn {
    display: none;
  }
  .dropbtn-icon {
    height: 100%;
    margin-right: 7px;
    float: right;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .topnav-title i {
    margin-left: 3px;
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative;
  }
  .topnav.responsive .dropbtn-icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive .dropdown {
    float: none;
  }
  .dropdown-content {
    display: block;
    width: 100%;
    text-align: left;
    border-radius: 0;
  }
}

.authenticated-component-body {
  width: 100%;
  display: flex;
}


.line-chart-body {
  height: 100%;
  width: 100%;
  z-index: 0;
}

.container {
  min-width: 310px;
  max-width: 800px;
  height: 400px;
  margin: 0 auto;
}

.line-chart-wrapper {
  height: 100%;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  z-index: 0;
  position: relative;
}

.pie-chart-body {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
}

.pie-chart-wrapper {
  height: 100%;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  z-index: 0;
  position: relative;
}

.circular-progressbar-body {
  width: 80px;
  float: left;
  margin: 5px;
}

.indoor-air-body {
  margin-top: 70px;
}

.chart-row1, .chart-row2 {
  width: 98%;
  margin-left: auto;
  margin-right: auto;
}

.chart-column {
  float: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 8px;
}

.chart-row1 .chart-column .chart {
  width: 100%;
  height: 360px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  background-color: white;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.05);
}

.chart-row2 .chart-column .chart {
  width: 100%;
  height: 120px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  background-color: white;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.05);
}

.chart-controller .chart-column .controller {
  width: 98%;
  height: 80px;
  padding-top: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  background-color: white;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.05);
}

.controller .date-picker{
  padding: 8px;
}

.react-datepicker {
  font-size: 1.3rem !important;
}

.react-datepicker__current-month {
  font-size: 1.5rem !important;
}

.react-datepicker__header {
  padding-top: 6px !important;
}

.react-datepicker__navigation {
  top: 13px !important;
}

.react-datepicker__day-name, .react-datepicker__day {
  margin: 0.5rem !important;
}

.side {
  width: 33.3%;
}

.middle {
  width: 70%;
}

.full {
  width: 100%;
}

@media screen and (max-width: 600px) {
  .chart-row1 .chart-column.side, .chart-row1 .chart-column.middle {
    width: 100%;
  }
  .chart-row2 .chart-column.side, .chart-row2 .chart-column.middle {
    width: 100%;
  }
  .body {
    margin-left: 0;
  }
}

@media screen and (min-width: 1300px) {
  .chart-row1 .chart-column.full {
    width: 100%;
  }
}

.login-body {
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.header {
  background-color: rgba(54, 65, 80, 1);
  height: 70px;
  display: flex;
  align-items: center;
}

.header h1 {
  color: white;
  margin: 0;
  padding-left: 10px;
  font-weight: bold;
  letter-spacing: -1px;
  font-size: 24px;
}

.form-body {
  border-radius: 0px 0px 7px 7px;
  border-width: 0px 1px 1px 1px;
  border-color: rgba(54, 65, 80, 1);
  border-style: solid;
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 15px 40px 40px 40px;
}

.form-body label {
  float: left;
  font-weight: 400;
  font-size: 18px;
  margin-left: 5px;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset;
}

.form-header {
  margin-left: auto;
  margin-right: auto;
  height: 50px;
  width: 500px;
  margin-top: 70px;
  --background-color: #008DD5;
  background-color: rgba(54, 65, 80, 1);
  border-radius: 7px 7px 0px 0px;
  --color: 008DD5;
}

.form-input {
  width: 100%;
  padding: 10px 20px;
  margin-bottom: 12px;
  display: inline-block;
  border: 1px solid;
  border-color: #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: medium;
  font-size: initial;
  transition: border-color 200ms linear;
}

.form-input:hover {
    transition: border-color 200ms linear;
    border-color: #2e70e7;
}

.form-btn {
    width: 100%;
    --background-color: #008DD5;
    background-color: rgba(54, 65, 80, 1);
    font-size: 20px;
    color: white;
    padding: 8px 20px;
    margin: 12px 0;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 200ms linear;
}

.form-btn:hover {
    background-color: #1e4ea3;
    transition: background-color 200ms linear;
}

@media only screen and (max-width: 600px) {
  .form-body {
    width: 100%;
    border: none;
    height: 100vh;
    margin: 0;
    padding-top: 20px;
  }
  .form-header {
    --display: none;
    width: 100%;
    margin-top: 0;
    border-radius: 0;
  }
}

.camera-body {
  height: 100vh !important;
  top: 0;
  margin-left: 120px;
  background-color: rgba(0, 0, 0, 0.03);
}

.stream-container img {
  padding-top: 120px;
  width: 70%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 600px) {
  .camera-body  {
    margin-left: 0;
  }
  .stream-container img {
    width: 90%;
  }
}

.dashboard-body {
  margin-top: 70px;
}

.plain-chart-row1 {
  width: 98%;
  margin-left: auto;
  margin-right: auto;
}

.plain-chart-row2 {
  width: 98%;
  margin-left: auto;
  margin-right: auto;
}

.plain-chart-column {
  margin-left: 5px;
  float: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 8px;
}

.plain-chart-row1 .plain-chart-column .chart-text {
  width: 100%;
  height: 50px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.05);
}

.chart-text p:nth-child(1) {
  float: left;
  margin-left: 15px;
  margin-top: 25px;
  font-size: 15px;
  font-weight: bold;
  color: white;
  border-radius: 50%;
  background-color: grey;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 45px;
  height: 45px;
  position: absolute;
  z-index: 1;
}

.chart-text p:nth-child(2) {
  float: left;
  margin-left: 64px;
  margin-top: 25px;
  font-size: 15px;
  font-weight: 600;
  color: grey;
  border-radius: 50%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 45px;
  height: 45px;
  text-align: left;
  position: absolute;
  z-index: 1;
}
.chart-text i {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
}
.chart-text p:nth-child(3) {
  float: left;
  margin-top: 5px;
  color: grey;
  position: absolute;
  margin-left: 15px;
  font-size: 12px;
  font-weight: bold;
}

.chart-text p:nth-child(4) {
  float: right;
  margin-top: 5px;
  color: grey;
  margin-right: 20px;
  margin-top: 20px;
  font-size: 20px;
}

.plain-chart-row1 .plain-chart-column .chart {
  width: 100%;
  height: 80px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  background-color: white;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.05);
}

.plain-chart-row2 .plain-chart-column .chart {
  width: 100%;
  height: 200px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  background-color: white;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.05);
}

.plain-chart-row1 .plain-chart-column.side {
  width: 30%;
}

.plain-chart-row1 .plain-chart-column.middle {
  width: 70%;
}

.plain-chart-row1 .plain-chart-column.full {
  width: 100%;
}

.plain-chart-row1 .plain-chart-column.plain {
  width: 230px;
}

.plain-chart-row2 .plain-chart-column.side {
  width: 33.3%;
}

.plain-chart-row2 .plain-chart-column.plain {
  width: 33.3%;
}

.chart_rows:after {
  content: "";
  display: table;
  clear: both;
}

@media screen and (max-width: 660px) {
  .plain-chart-row1 .plain-chart-column.side, .plain-chart-row1 .plain-chart-column.plain {
    width: 100%;
  }
  .plain-chart-row2 .plain-chart-column.side, .plain-chart-row2 .plain-chart-column.plain {
    width: 100%;
  }

}

@media screen and (min-width: 1300px) {
  .plain-chart-row1 .plain-chart-column.full {
    width: 90%;
  }
}

html,
body {
  margin: 0;
  background-color: rgba(242, 242, 250);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.app-body {
  display: flex;
}

.component-body {
  flex: 1 1;
  flex-grow: 1;
}

.App-loader {
  border: 4px solid #DFDFDF;
  border-radius: 50%;
  border-top: 4px solid #757575;
  width: 45px;
  height: 45px;
  -webkit-animation: spin-loader 1s linear infinite;
  animation: spin-loader 1s linear infinite;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  top: 40%;
  left: 50%;
}

.Graph-loader {
  border: 4px solid #DFDFDF;
  border-radius: 50%;
  border-top: 4px solid #757575;
  width: 45px;
  height: 45px;
  -webkit-animation: spin-loader 1s linear infinite;
  animation: spin-loader 1s linear infinite;
  margin: auto;
  z-index: 1;
  position: relative;
}

